/* The Offline Modal (background) */
.wrs_modal_offline {
  display: none;
  /* Hidden by default */
  position: fixed;
  /* Stay in place */
  z-index: 2;
  /* Sit on top */
  padding-top: 150px;
  /* Location of the box */
  left: 0;
  top: 0;
  width: 100%;
  /* Full width */
  height: 100%;
  /* Full height */
  overflow: auto;
  /* Enable scroll if needed */
  background-color: rgb(0, 0, 0);
  /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4);
  /* Black w/ opacity */
  margin: auto;
}

/* Modal Content */
.wrs_modal_content_offline {
  margin: auto;
  padding: 16px;
  background: #fff7ed;
  border-radius: 6px;
  width: 517px;
  height: 100px;
  position: relative;
}

/* The Close Button */
.wrs_modal_offline_close {
  color: #c2410c;
  font-size: 24px;
  font-weight: bold;
  left: 95.7%;
  right: 2.08%;
  top: 7.6%;
  bottom: 75%;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  position: absolute;
}

/* The Warn Icon */
.wrs_modal_offline_warn {
  position: absolute;
  left: 2.08%;
  right: 94%;
  top: 11.6%;
  bottom: 75%;
  font-size: 24px;
  color: #fb923c;
  background-repeat: no-repeat;
}

.wrs_modal_offline_text_container {
  position: absolute;
  left: 6.8%;
  right: 6.08%;
  top: 10.4%;
  bottom: 2%;
}

.wrs_modal_offline_text {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #c2410c;
}

.wrs_modal_offline_text_warn {
  height: 25px;
  font-family: "Inter";
  font-style: normal;
  font-size: 14px;
  line-height: 20px;
  font-weight: bold;
  color: #c2410c;
}

.wrs_modal_offline_close:hover,
.wrs_modal_offline_close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}
